*{
  font-family: 'PT Serif', serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  box-sizing: border-box;

}

.white{
  color:white;
  font-size: 20px;
  font-weight: bold;
}
.editor{
  text-align: center;
  background-color: #ABC064;
}

#post-name{
  font-size: 15px;
  color: white;
  display: block;
  background-color: transparent;
  margin: 1em auto;
  border: none;
  border-bottom: 1px solid white;
  outline: none;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

#post-name:focus{
  border-bottom: 1px solid black;
}

#post{
  font-size: 15px;
  color: white;
  background-color: transparent;
  border: 1px solid white;
  outline: none;
  margin: 2em auto;
  display: block;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

#post:focus{
  border: 1px solid black;
}

#btn{
  color:white;
  border: none;
  background-color:#726232;
  padding: 10px 20px;
  outline: none;
  border-radius: 5%;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  margin-bottom: 10px;
}

#btn:hover{
  color:black;
}

#btn:focus{
  -webkit-animation-name: btnClick;
          animation-name: btnClick;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

@-webkit-keyframes btnClick {
  0%   {color:black; background-color:#726232; }
  50%  {color:white; background-color:#AFD5AD;}
  100%  {color:black;background-color:#726232;}
}

@keyframes btnClick {
  0%   {color:black; background-color:#726232; }
  50%  {color:white; background-color:#AFD5AD;}
  100%  {color:black;background-color:#726232;}
}

.post-container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.post{
  width:450px;
  min-height:280px;
  margin-right: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid grey;
}

.post h1{
  text-align: center;
}

.post p{
  text-align: left;
}

*{
  margin: 0;
  padding:0;
}

.logo{
  text-align: center;
  background-color: #716231;
  font-family: 'PT Serif', serif;
  color:white;
  padding: 20px;
  position: -webkit-sticky;
  position: sticky;
  top:0;
  z-index:10;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

